    monospace;
}

.circle-btn {
  background-color: #f5f5f5;
  width: 1.5em;
  height: 1.5em;
  border-radius: 5em;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bold;
  font-size: 1em;
  color: #353744; }
  .circle-btn > * {
    -webkit-transition-duration: 300ms;
            transition-duration: 300ms;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform; }
  .circle-btn:hover > * {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  .circle-btn:active {
    opacity: 50%; }
  .circle-btn .text {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start; }
  .circle-btn > * {
    width: 15px;
    height: 15px; }
    .circle-btn > * path {
      fill: #353744; }

.logo-container {
  grid-area: page;
  width: -moz-fit-content;
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .logo-container .logo-text {
    color: #50d19e;
    font-size: 2em;
    font-style: italic;
    font-weight: bold;
    margin-right: 0.2em; }

@media (max-width: 679px) {
  .navbar-container {
    grid-template-columns: minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em) minmax(1fr, 3em);
    grid-template-rows: 1fr 1fr;
    grid-template-areas: ". logo logo sitenav ." "roomnav roomnav roomnav roomnav roomnav"; } }

@media (min-width: 680px) {
  .navbar-container {
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-areas: "logo roomnav sitenav"; } }

.navbar-container {
