  padding: 0 0.5em;
  box-sizing: border-box;
  display: grid; }
  .navbar-container .logo-section {
    grid-area: logo; }
  .navbar-container .room-nav {
    grid-area: roomnav;
    display: flex;
    justify-content: center;
    align-items: center; }
    .navbar-container .room-nav .circle-hover:hover {
      background-color: #cacaca; }
    .navbar-container .room-nav .room-nav-btn {
      margin: 0 0.15em; }
    .navbar-container .room-nav .home-btn,
    .navbar-container .room-nav .search-btn,
    .navbar-container .room-nav .create-room-btn {
      background-color: transparent; }
      .navbar-container .room-nav .home-btn svg path,
      .navbar-container .room-nav .search-btn svg path,
      .navbar-container .room-nav .create-room-btn svg path {
