      background-color: #e9524a; }

.group {
  position: relative;
  margin: 20px 0px 35px 0px; }
  .group .form-input {
    font-family: "Open Sans Condensed";
    color: #26262b;
    box-sizing: border-box;
    background: none;
    background-color: #f6f6f6;
    font-size: 18px;
    font-weight: bold;
    padding: 5px 10px 5px 10px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 4px solid #50d19e;
    margin: 20px 0px 0px 0px; }
    .group .form-input:focus {
      outline: none; }
    .group .form-input:focus ~ .form-input-label {
      top: -22px;
      left: 2px;
      font-size: 12px;
      font-weight: bold;
      color: white; }
  .group input[type="password"] {
    letter-spacing: 0.3em; }
  .group .form-input-label {
    color: #2e2e31;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 12px;
    top: 7px;
    -webkit-transition: 300ms ease all;
    transition: 300ms ease all; }
    .group .form-input-label.shrink {
      top: -22px;
      left: 2px;
      font-size: 12px;
      font-weight: bold;
      color: white; }

.custom-button {
